import React from "react";
import TweetItem from "./TweetItem";
import Axios from "axios";
import AddTweet from "./AddTweet";

class MainPage extends React.Component {
    state = { tweets: [], currentUser: { username: "" }, showScrollButton: false };

    componentDidMount() {
        Axios.get("/api/tweets", { withCredentials: true }).then(res => {
            this.setState({ tweets: res.data.reverse() });
        });
        setTimeout(() => {
            Axios.get("/api/getcurrentuser", { withCredentials: true }).then(res => {
                this.setState({ currentUser: res.data });
            });
        }, 500);
        window.addEventListener("scroll", this.handleScroll);
    }

    componentWillUnmount() {
        window.removeEventListener("scroll", this.handleScroll);
    }

    handleScroll = () => {
        const scrollY = window.scrollY || document.documentElement.scrollTop;
        this.setState({ showScrollButton: scrollY > 100 });
    };

    scrollToTop = () => {
        window.scrollTo({ top: 0, behavior: "smooth" });
    };

    render() {
        return (
            <div style={styles.container}>
                <main style={styles.mainContent}>
                    <div style={styles.header}>
                        <button 
                            style={styles.newTweetButton}
                            onClick={() => {
                                document.getElementById("addTweet").style.display = "block";
                            }}
                        >
                            + New Tweet
                        </button>
                    </div>

                    <AddTweet />
                    
                    {this.state.tweets.length === 0 ? (
                        <div style={styles.emptyState}>
                            <p style={styles.emptyText}>No tweets to display</p>
                            <p style={styles.emptySubtext}>Start by creating your first tweet</p>
                        </div>
                    ) : (
                        <div style={styles.tweetsContainer}>
                            {this.state.tweets.map((item, index) => (
                                <TweetItem
                                    id={item.id}
                                    title={item.title}
                                    content={item.content}
                                    author={item.user.username}
                                    isOwner={this.state.currentUser.username === item.user.username}
                                    isOffensive={item.is_offensive}
                                    key={index}
                                />
                            ))}
                        </div>
                    )}
                </main>
                {this.state.showScrollButton && (
                    <button 
                        style={styles.scrollToTopButton} 
                        onClick={this.scrollToTop}
                    >
                        ↑
                    </button>
                )}
            </div>
        );
    }
}


const styles = {
    container: {
        backgroundColor: '#ffffff',
        minHeight: '100vh',
        padding: '2rem 0',
    },
    mainContent: {
        maxWidth: '700px',
        margin: '0 auto',
        padding: '0 1rem',
    },
    header: {
        marginBottom: '2rem',
        textAlign: 'center',
    },
    newTweetButton: {
        padding: '0.75rem 2rem',
        backgroundColor: '#2563eb',
        color: 'white',
        border: 'none',
        borderRadius: '6px',
        fontSize: '1rem',
        fontWeight: '500',
        cursor: 'pointer',
        transition: 'all 0.2s ease',
        ':hover': {
            backgroundColor: '#1d4ed8',
            transform: 'translateY(-1px)',
        },
    },
    tweetsContainer: {
        display: 'flex',
        flexDirection: 'column',
        gap: '1.5rem',
    },
    emptyState: {
        textAlign: 'center',
        padding: '4rem 0',
        backgroundColor: '#f8fafc',
        borderRadius: '8px',
        marginTop: '2rem',
    },
    emptyText: {
        fontSize: '1.1rem',
        color: '#1e293b',
        margin: 0,
        fontWeight: '500',
    },
    emptySubtext: {
        fontSize: '0.9rem',
        color: '#64748b',
        margin: '0.5rem 0 0',
    },
    scrollToTopButton: {
        position: 'fixed',
        bottom: '20px',
        right: '20px',
        padding: '1rem',
        backgroundColor: '#2563eb',
        color: 'white',
        border: 'none',
        borderRadius: '50%',
        cursor: 'pointer',
        fontSize: '1.5rem',
        boxShadow: '0 2px 4px rgba(0, 0, 0, 0.2)',
        transition: 'all 0.2s ease',
        ':hover': {
            backgroundColor: '#1d4ed8',
            transform: 'translateY(-2px)',
        },
    },
};

export default MainPage;

