
import React from "react";

function Home() {
    return (
        <div className="w3-container" style={{ 
            backgroundColor: "#f0f2f5", 
            minHeight: "100vh",
            fontFamily: "'Inter', sans-serif"
        }}>
            {/* Hero Section */}
            <div className="w3-center" style={{ 
                padding: "8rem 2rem 10rem",
                background: "linear-gradient(135deg, rgba(29,161,242,1) 0%, rgba(16,137,214,1) 100%)",
                color: "white",
                clipPath: "polygon(0 0, 100% 0, 100% 90%, 0 100%)",
                marginBottom: "-4rem"
            }}>
                <div className="w3-animate-opacity">
                    <h1 style={{ 
                        fontSize: "4.5rem",
                        fontWeight: 800,
                        letterSpacing: "-2px",
                        marginBottom: "1.5rem",
                        fontFamily: "'Poppins', sans-serif",
                        background: "linear-gradient(45deg, #fff 30%, #c3e4ff)",
                        WebkitBackgroundClip: "text",
                        WebkitTextFillColor: "transparent",
                        filter: "drop-shadow(0 4px 8px rgba(0,0,0,0.15))"
                    }}>
                        TØLD
                        <span style={{ 
                            fontSize: "0.35em",
                            display: "block",
                            fontWeight: 400,
                            marginTop: "1.5rem",
                            fontFamily: "'Inter', sans-serif",
                            background: "none",
                            WebkitTextFillColor: "rgba(255,255,255,0.9)",
                            letterSpacing: "0.5px"
                        }}>
                            Telugu Offensive Language Detection
                        </span>
                    </h1>
                    
                    <div className="w3-margin-top" style={{ marginTop: "3rem" }}>
                        <button
                            className="w3-button w3-round-xlarge"
                            style={{
                                padding: "16px 40px",
                                margin: "0 1rem",
                                transition: "all 0.3s cubic-bezier(0.4, 0, 0.2, 1)",
                                fontWeight: 600,
                                background: "rgba(255,255,255,0.15)",
                                backdropFilter: "blur(8px)",
                                border: "2px solid rgba(255,255,255,0.3)",
                                fontSize: "1.1em"
                            }}
                            onMouseOver={(e) => e.currentTarget.style.transform = "translateY(-2px)"}
                            onMouseOut={(e) => e.currentTarget.style.transform = "none"}
                            onClick={() => window.location = "/register"}
                        >
                            🚀 Join Now
                        </button>
                        <button
                            className="w3-button w3-round-xlarge"
                            style={{
                                padding: "16px 40px",
                                margin: "0 1rem",
                                transition: "all 0.3s cubic-bezier(0.4, 0, 0.2, 1)",
                                fontWeight: 600,
                                background: "transparent",
                                border: "2px solid rgba(255,255,255,0.3)",
                                fontSize: "1.1em"
                            }}
                            onMouseOver={(e) => {
                                e.currentTarget.style.background = "rgba(255,255,255,0.15)";
                                e.currentTarget.style.transform = "translateY(-2px)";
                            }}
                            onMouseOut={(e) => {
                                e.currentTarget.style.background = "transparent";
                                e.currentTarget.style.transform = "none";
                            }}
                            onClick={() => window.location = "/login"}
                        >
                            🔑 Sign In
                        </button>
                    </div>
                </div>
            </div>

            {/* Trending Section */}
            <div className="w3-row-padding" style={{ 
                padding: "6rem 2rem",
                maxWidth: "1400px",
                margin: "0 auto"
            }}>
                <h2 style={{ 
                    fontSize: "2.5rem",
                    fontWeight: 700,
                    color: "#1a1a1a",
                    marginBottom: "4rem",
                    textAlign: "center",
                    fontFamily: "'Poppins', sans-serif",
                    position: "relative",
                    display: "inline-block",
                    left: "50%",
                    transform: "translateX(-50%)",
                    padding: "0 2rem"
                }}>
                    <span style={{
                        background: "linear-gradient(120deg, #1DA1F2 0%, #0d8ddb 100%)",
                        WebkitBackgroundClip: "text",
                        WebkitTextFillColor: "transparent"
                    }}>
                        Trending Now
                    </span>
                    <div style={{
                        position: "absolute",
                        bottom: "-10px",
                        left: "50%",
                        transform: "translateX(-50%)",
                        width: "60%",
                        height: "4px",
                        background: "linear-gradient(90deg, transparent 0%, #1DA1F2 50%, transparent 100%)"
                    }}></div>
                </h2>

                <div className="w3-row" style={{ display: "flex", flexWrap: "wrap" }}>
                    {[1, 2, 3].map((item) => (
                        <div className="w3-third" key={item} style={{ 
                            display: "flex",
                            padding: "0 1rem",
                            marginBottom: "2rem"
                        }}>
                            <div className="w3-card w3-round-xlarge" style={{
                                width: "100%",
                                transition: "all 0.3s cubic-bezier(0.4, 0, 0.2, 1)",
                                cursor: "pointer",
                                background: "rgba(255,255,255,0.7)",
                                backdropFilter: "blur(12px)",
                                border: "1px solid rgba(255,255,255,0.3)",
                                boxShadow: "0 8px 32px rgba(0,0,0,0.05)",
                                display: "flex",
                                flexDirection: "column"
                            }}
                            onMouseOver={(e) => e.currentTarget.style.transform = "translateY(-8px)"}
                            onMouseOut={(e) => e.currentTarget.style.transform = "none"}
                            >
                                <div className="w3-padding" style={{ 
                                    padding: "1.5rem",
                                    flex: 1,
                                    display: "flex",
                                    flexDirection: "column"
                                }}>
                                    <div className="w3-row" style={{ 
                                        alignItems: "flex-start",
                                        flex: 1
                                    }}>
                                        <div className="w3-col" style={{ width: "60px" }}>
                                            <div style={{
                                                borderRadius: "50%",
                                                overflow: "hidden",
                                                width: "60px",
                                                height: "60px",
                                                border: "2px solid #fff",
                                                boxShadow: "0 4px 8px rgba(0,0,0,0.1)"
                                            }}>
                                                <img 
                                                    src={`https://i.pravatar.cc/120?img=${item * 3}`} 
                                                    style={{ width: "100%", height: "100%", objectFit: "cover" }}
                                                    alt="avatar"
                                                />
                                            </div>
                                        </div>
                                        <div className="w3-rest w3-padding-left" style={{ 
                                            paddingLeft: "1.5rem",
                                            flex: 1,
                                            display: "flex",
                                            flexDirection: "column"
                                        }}>
                                            <div style={{ 
                                                fontSize: "1.1em",
                                                fontWeight: 600,
                                                color: "#1a1a1a",
                                                display: "flex",
                                                alignItems: "center",
                                                gap: "0.5rem"
                                            }}>
                                                @User_{item}
                                                <span style={{ 
                                                    fontSize: "0.8em",
                                                    fontWeight: 400,
                                                    color: "#666"
                                                }}>· {item * 2}h</span>
                                            </div>
                                            <p style={{ 
                                                fontSize: "1em",
                                                lineHeight: 1.6,
                                                color: "#444",
                                                margin: "1rem 0",
                                                flex: 1
                                            }}>
                                                {item === 1 && "Quantum Computing వల్ల డేటా హ్యాకింగ్ easy? అందుకే Cyber Security next level కి వెళ్ళాలి 💻 \n\n\n"}
                                                {item === 2 && "Full Krushiga purthiga fight pettisisi… 'Enti ee IPL fixing la undi' anna valllu asalaina cricket pandithulu!\n\n\n"}
                                                {item === 3 && "🚨5G కూడా ఇంకా ఫుల్‌గా వచ్చేది కాదు… ఇప్పుడు 6G పై R&D మొదలు 😲 Future Speed unimaginable!\n\n\n"}
                                            </p>
                                            <div style={{ 
                                                display: "flex",
                                                gap: "1.5rem",
                                                color: "#666",
                                                fontSize: "0.9em"
                                            }}>
                                                <span style={{ display: "flex", alignItems: "center", gap: "0.5rem" }}>
                                                    <i className="fa fa-heart" style={{ color: "#ff4081" }}></i> 
                                                    {item === 1 ? "2.1K" : item === 2 ? "3.8K" : "5.6K"}
                                                </span>
                                                <span style={{ display: "flex", alignItems: "center", gap: "0.5rem" }}>
                                                    <i className="fa fa-retweet" style={{ color: "#1DA1F2" }}></i> 
                                                    {item === 1 ? "489" : item === 2 ? "1.2K" : "2.3K"}
                                                </span>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    ))}
                </div>
            </div>

            {/* Footer */}
            <footer style={{ 
                background: "linear-gradient(135deg, rgba(29,161,242,1) 0%, rgba(16,137,214,1) 100%)",
                color: "white",
                padding: "4rem 2rem",
                clipPath: "polygon(0 20%, 100% 0, 100% 100%, 0 100%)",
                marginTop: "6rem"
            }}>
                <div style={{ 
                    maxWidth: "1200px",
                    margin: "0 auto",
                    textAlign: "center"
                }}>
                    <div style={{ 
                        fontSize: "1.5rem",
                        fontWeight: 600,
                        marginBottom: "1.5rem",
                        fontFamily: "'Poppins', sans-serif"
                    }}>
                        TØLD
                    </div>
                    <div style={{ 
                        display: "flex",
                        justifyContent: "center",
                        gap: "2rem",
                        marginBottom: "2rem"
                    }}>
                        <a href="#" style={{ color: "white", textDecoration: "none" }}>About</a>
                        <a href="#" style={{ color: "white", textDecoration: "none" }}>Git</a>
                        <a href="#" style={{ color: "white", textDecoration: "none" }}>Docx</a>
                    </div>
                    <div style={{ 
                        fontSize: "0.9em",
                        color: "rgba(255,255,255,0.8)"
                    }}>
                        © 2025 TØLD · Telugu Offensive Language Detection · #MajorProject
                    </div>
                </div>
            </footer>

            <style>
                {`
                @import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;600;700&family=Inter:wght@400;500;600&display=swap');
                
                button {
                    backdrop-filter: blur(8px);
                    -webkit-backdrop-filter: blur(8px);
                }
                
                .w3-card:hover {
                    box-shadow: 0 12px 24px rgba(0,0,0,0.1) !important;
                }
                
                @keyframes float {
                    0% { transform: translateY(0px); }
                    50% { transform: translateY(-10px); }
                    100% { transform: translateY(0px); }
                }
                `}
            </style>
        </div>
    );
}

export default Home;

