
import React, { Component } from "react";
import Alert from "./Alert";
import { login, check } from "../login";

class Login extends Component {
    state = { err: "" };

    componentDidMount() {
        check().then(r => { if (r) { window.location = "/" } })
    }

    login = (e) => {
        e.preventDefault();
        login(document.getElementById("email").value,
            document.getElementById("password").value).then(r => {
                if (r === true) {
                    window.location = "/"
                } else {
                    this.setState({ err: r })
                }
            })
    };

    render() {
        return (
            <div style={styles.pageContainer}>
                <div style={styles.cardContainer}>
                    <div style={styles.header}>
                        <h2 style={styles.title}>Welcome Back</h2>
                        <p style={styles.subtitle}>Sign in to continue</p>
                    </div>
                    
                    <div style={styles.formContainer}>
                        {this.state.err.length > 0 && (
                            <Alert
                                message={`Check your form and try again! (${this.state.err})`}
                                style={styles.alert}
                            />
                        )}
                        
                        <form onSubmit={this.login} style={styles.form}>
                            <div style={styles.inputGroup}>
                                <label htmlFor="email" style={styles.label}>Email</label>
                                <input
                                    type="email"
                                    id="email"
                                    style={styles.input}
                                    placeholder="Enter your email"
                                />
                            </div>
                            
                            <div style={styles.inputGroup}>
                                <label htmlFor="password" style={styles.label}>Password</label>
                                <input
                                    type="password"
                                    id="password"
                                    style={styles.input}
                                    placeholder="Enter your password"
                                />
                            </div>
                            
                            <button type="submit" style={styles.button}>
                                Sign In
                            </button>
                        </form>
                        
                        <div style={styles.footer}>
                            <p style={styles.footerText}>
                                Don't have an account? <a href="/register" style={styles.link}>Sign up</a>
                            </p>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

const styles = {
    cardContainer: {
        backgroundColor: 'white',
        borderRadius: '16px',
        boxShadow: '0 8px 30px rgba(0,0,0,0.12)',
        width: '100%',
        maxWidth: '400px',
        overflow: 'hidden',
        margin: '10rem auto 2rem',
    },
    header: {
        padding: '2rem',
        background: 'linear-gradient(135deg, #6366f1 0%, #a855f7 100%)',
        color: 'white',
        textAlign: 'center',
    },
    title: {
        margin: 0,
        fontSize: '1.8rem',
        fontWeight: '700',
    },
    subtitle: {
        margin: '0.5rem 0 0',
        fontSize: '0.9rem',
        opacity: 0.9,
    },
    formContainer: {
        padding: '2rem',
    },
    inputGroup: {
        marginBottom: '1.5rem',
    },
    label: {
        display: 'block',
        marginBottom: '0.5rem',
        fontSize: '0.875rem',
        color: '#374151',
        fontWeight: '500',
    },
    input: {
        width: '100%',
        padding: '0.75rem',
        borderRadius: '8px',
        border: '1px solid #e5e7eb',
        fontSize: '1rem',
        transition: 'all 0.3s ease',
    },
    button: {
        width: '100%',
        padding: '0.875rem',
        backgroundColor: '#6366f1',
        color: 'white',
        border: 'none',
        borderRadius: '8px',
        fontSize: '1rem',
        fontWeight: '600',
        cursor: 'pointer',
        transition: 'all 0.3s ease',
    },
    footer: {
        marginTop: '1.5rem',
        textAlign: 'center',
    },
    footerText: {
        color: '#6b7280',
        fontSize: '0.875rem',
    },
    link: {
        color: '#6366f1',
        fontWeight: '600',
        textDecoration: 'none',
    },
    alert: {
        marginBottom: '1.5rem',
    }
};

export default Login;

