
import React from "react";

function Navbar() {
    const [theme, setTheme] = React.useState(localStorage.getItem("theme") || "light");
    const [sessionActive, setSessionActive] = React.useState(
        localStorage.getItem("sessionActive") === "true"
    );

    React.useEffect(() => {
        document.body.classList.toggle("dark", theme === "dark");
    }, [theme]);

    React.useEffect(() => {
        const handleStorageChange = () => {
            setSessionActive(localStorage.getItem("sessionActive") === "true");
        };
        window.addEventListener("storage", handleStorageChange);
        return () => window.removeEventListener("storage", handleStorageChange);
    }, []);

    const navItems = [
        { name: sessionActive ? "Settings" : "Login", link: sessionActive ? "/settings" : "/login" },
        { name: sessionActive ? "Logout" : "Register", link: sessionActive ? "/logout" : "/register" }
    ];

    return (
        <nav style={{
            position: "fixed",
            top: 0,
            width: "100%",
            zIndex: 1000,
            background: theme === "dark" 
                ? "rgba(0, 0, 0, 0.85)" 
                : "rgba(255, 255, 255, 0.85)",
            backdropFilter: "blur(12px)",
            borderBottom: theme === "dark" 
                ? "1px solid rgba(255, 255, 255, 0.1)" 
                : "1px solid rgba(0, 0, 0, 0.1)",
            boxShadow: "0 4px 30px rgba(0, 0, 0, 0.1)"
        }}>
            <div style={{
                maxWidth: "1400px",
                margin: "0 auto",
                padding: "1rem 2rem",
                display: "flex",
                justifyContent: "space-between",
                alignItems: "center"
            }}>
                {/* Logo */}
                <a href="/" style={{
                    fontSize: "1.8rem",
                    fontWeight: 700,
                    textDecoration: "none",
                    background: "linear-gradient(45deg, #1DA1F2, #0d8ddb)",
                    WebkitBackgroundClip: "text",
                    WebkitTextFillColor: "transparent",
                    fontFamily: "'Poppins', sans-serif",
                    display: "flex",
                    alignItems: "center",
                    gap: "0.5rem"
                }}>
                    TØLD
                </a>

                {/* Desktop Navigation */}
                <div style={{ 
                    display: "flex",
                    alignItems: "center",
                    gap: "1.5rem"
                }}>
                    {/*<button
                        onClick={() => {
                            const newTheme = theme === "dark" ? "light" : "dark";
                            localStorage.setItem("theme", newTheme);
                            setTheme(newTheme);
                        }}
                        style={{
                            background: "none",
                            border: "none",
                            cursor: "pointer",
                            fontSize: "1.5rem",
                            padding: "0.5rem",
                            borderRadius: "50%",
                            transition: "all 0.3s ease",
                            transform: theme === "dark" ? "rotate(360deg)" : "rotate(0deg)"
                        }}
                    >
                        {theme === "dark" ? "🌞" : "🌙"}
                    </button>*/}

                    {navItems.map((item, index) => (
                        <a
                            key={item.name}
                            href={item.link}
                            style={{
                                padding: "0.8rem 1.5rem",
                                borderRadius: "2rem",
                                textDecoration: "none",
                                fontWeight: 500,
                                transition: "all 0.3s cubic-bezier(0.4, 0, 0.2, 1)",
                                background: index === 1 ? 
                                    (theme === "dark" ? "rgba(255, 75, 75, 0.15)" : "rgba(255, 75, 75, 0.1)") : "none",
                                color: index === 1 ? "#ff4b4b" : theme === "dark" ? "#fff" : "#1a1a1a",
                                border: index === 1 ? 
                                    "1px solid rgba(255, 75, 75, 0.3)" : "1px solid transparent",
                                display: "flex",
                                alignItems: "center",
                                gap: "0.5rem"
                            }}
                            onMouseOver={(e) => {
                                if(index === 1) return;
                                e.currentTarget.style.background = theme === "dark" 
                                    ? "rgba(255, 255, 255, 0.05)" 
                                    : "rgba(0, 0, 0, 0.05)";
                            }}
                            onMouseOut={(e) => {
                                if(index === 1) return;
                                e.currentTarget.style.background = "none";
                            }}
                        >
                            {item.name}
                        </a>
                    ))}
                </div>
            </div>

            <style>
                {`
                @import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700&display=swap');
                
                body{
                    margin-top: 80px;
                }

                body.dark {
                    background: #0a0a0a;
                    color: #fff;
                }

                @keyframes rotate {
                    from { transform: rotate(0deg); }
                    to { transform: rotate(360deg); }
                }
                `}
            </style>
        </nav>
    );
}

export default Navbar;

